<template>
  <div>
    <Carousel loop v-model="value" id="home">
      <CarouselItem v-for="(obj,index) in loopList" :key="index" style="min-height: 300px;max-height: 600px">
        <img :src="obj" alt="" style="object-fit:cover;width: 100%;min-height: 300px;max-height: 600px;">
      </CarouselItem>
    </Carousel>

    <Row type="flex" justify="center" align="center" style="text-align: center;padding: 50px 0;">
      <Col span="24" id="product">
      <h1>产品介绍</h1>
      <p>—造型丰富、色彩多样—</p>
      </Col>
    </Row>


    <div style="max-width: 1065px;margin: 0 auto">
      <Row type="flex" justify="center" align="center" style="text-align: center">
        <Col :xs="{span:24}" :sm="{span:11}" :md="{span:7}" :lg="{span:7}" v-for="(obj,index) in productList"
             :key="index"
             style="padding: 5px;">
        <img :src="obj.pic" alt="" style="object-fit:cover;background-size: 60% 60%" @mouseenter="enter(index)"  v-show="!(active===index)">
        <Card v-show="active===index" @mouseleave="leave(index)">
          <Row>
            <Col :xs="{span:24}" :sm="{span:11}" :md="{span:11}" :lg="{span:11}" v-for="(subChild,index) in obj.child" :key="index">
            <img :src="subChild" alt=""
                 style="object-fit:cover;max-width: 100px;max-height: 100px">
            </Col>
          </Row>
        </Card>

        </Col>
      </Row>

    </div>
    <Row type="flex" justify="center" align="center" style="text-align: center;padding: 50px 0;">
      <Col span="24">
      <h1>产品适应场景</h1>
      <p>—扫码即用、实时定位—</p>
      </Col>
    </Row>
    <div style="max-width: 1065px;margin: 0 auto">
      <Row type="flex" justify="center" align="center" style="text-align: center">
        <Col :xs="{span:24}" :sm="{span:11}" :md="{span:7}" :lg="{span:7}" v-for="(obj,index) in sceneList" :key="index"
             style="padding: 5px;">
        <img :src="obj" alt="" style="object-fit:cover;background-size: 100% 100%;width: 200px;height: 200px;">
        </Col>
      </Row>
    </div>
    <div style="max-width: 1065px;margin: 100px auto">
      <Row type="flex" justify="start" align="center" style="text-align: center">
        <Col :xs="{span:24}" :sm="{span:10}" :md="{span:10}" :lg="{span:10}">
        <img :src="introduce" alt=""
             style="object-fit:cover;background-size: 100% 100%;width: 300px;height: 200px;max-width: 400px">
        </Col>
        <Col :xs="{span:24}" :sm="{span:12}" :md="{span:12}" :lg="{span:12}">
        <h1 style="color: #1c2438">公司介绍</h1>
        <h3 style="text-align: left;text-indent: 25px;margin-top: 25px;line-height: 35px;color:#495060;">
          小司机,坐落于杭州,成立之初即获得数百万天使轮融资。通过自主研发的商用级智能玩具车,
          重新定义儿童玩具车市场;打造全新的供应链模式,改变玩具车消费方式。旨在成为最具影响
          力的儿童智能车供应链服务平台。</h3>
        </Col>
      </Row>
    </div>
    <Row type="flex" justify="center" align="center" style="text-align: center;padding: 0 0 50px;">
      <Col span="24">
      <h1>团队核心成员</h1>
      <p>—配合默契、执行力强—</p>
      </Col>
    </Row>
    <div style="max-width: 1065px;margin: 0 auto">
      <Row type="flex" justify="center" align="center" style="text-align: center">
        <Col :xs="{span:24}" :sm="{span:11}" :md="{span:5,offset:1}" :lg="{span:5,offset:1}"
             v-for="(obj,index) in people" :key="index">
        <img :src="obj.pic" alt="" style="Object-fit:cover;background-size: 100% 100%;width: 100px;height: 100px;">
        <p style="padding: 25px 0 0;"><span>{{obj.name}}</span><span>{{obj.position}}</span></p>
        <p v-if="obj.duty">({{obj.duty}})</p>
        <p v-if="obj.introduce"
           :style="{textAlign: 'left',textIndent: '25px',marginTop: (25+((obj.duty==='')?18:0))+'px'}">
          {{obj.introduce}}</p>
        </Col>

      </Row>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'home',
    data () {
      return {
        value: 0,
        loopList: [],
        productList: [
          {
            pic:'../static/car/carA.png',
            child:[
              '../static/car/carA1.jpg',
              '../static/car/carA2.jpg',
              '../static/car/carA3.jpg',
            ]
          },
          {
            pic:'../static/car/carB.png',
            child:[
              '../static/car/carB1.png',
              '../static/car/carB2.png',
              '../static/car/carB3.png',
            ]
          },
          {
            pic:'../static/car/carC.png',
            child:[
              '../static/car/carC1.jpg',
              '../static/car/carC2.jpg',
              '../static/car/carC3.jpg',
            ]
          },


        ],
        sceneList: [
          '../static/scene/sceneA.png',
          '../static/scene/sceneB.png',
          '../static/scene/sceneC.png',
        ],
        introduce: '../static/introduce/introduce.jpg',
        people: [
          {
            pic: '../static/people/peopleA.png',
            name: '何繁荣',
            position: '创始人&CEO',
            duty: '',
            college: '杭州电子科技⼤大学',
            introduce: '曾就职通用安吉星，原车音网（估值\n' +
            '26亿）总裁助理，车小米创始人，连\n' +
            '续创业者，擅长企业管理，有快速学\n' +
            '习能力，很强的带队能力和执行力，\n' +
            '10多年车联网从业经历，对儿童电动\n' +
            '玩具车的优化及升级有深刻认识。',
          },
          {
            pic: '../static/people/peopleB.png',
            name: '华威',
            position: '联合创始人&CTO',
            duty: '硬件负责人',
            college: '浙江大学',
            introduce: '杭州信雅达系统工程股份有限公司\n' +
            '项⽬经理，UT斯达康手机部高级工\n' +
            '程师，上海鹏盾电子商务股份有限\n' +
            '公司集团公司副总。\n' +
            '浙江⼤学控制理论与控制工程研究\n' +
            '生，精通软硬件开发及工业⾃动化,\n' +
            '物联网技术。',
          },
          {
            pic: '../static/people/peopleC.png',
            name: '张辉',
            position: '联合创始⼈人&CTO',
            duty: '软件负责人',
            college: '天津大学',
            introduce: '⽀付宝事业部高级工程师，淘宝\n' +
            '开放平台技术部主管，返利网技\n' +
            '术副总，10多年互联网平台和产\n' +
            '品设计及开发经验富有创业激\n' +
            '情。',
          },
          {
            pic: '../static/people/peopleD.png',
            name: '顾伊力',
            position: '联合创始⼈人&COO',
            duty: '',
            college: '浙江传媒学院',
            introduce: '杭州牟双⼲⼴广告有限公司创始⼈，7年\n' +
            '传媒公司经营，擅长各类活动策划\n' +
            '和执行，年销售额突破千万，曾多\n' +
            '次参与政府组织的⼤型公益活动，\n' +
            '渠道开拓能⼒，商务谈判能⼒强，\n' +
            '社会资源丰富。',
          },
        ],
        active:null,
      }
    },
    mounted () {

      this.loopList = [
        '../static/loop/loopA.jpg',
      ]
    },
    methods: {
      enter(val){
        this.active = val;
      },
      leave(val){
        this.active = null;
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .ivu-carousel-list {
    width: 100%;
  }
</style>



